(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .factory('Jobs', Jobs);

  /** @ngInject */
  function Jobs($firebaseObject) {
    var itemsRef = new Firebase(FIREBASE_URL + "/jobs");
    return $firebaseObject(itemsRef);
  }

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .config(routerConfig);

  /** @ngInject */
  function routerConfig($stateProvider, $urlRouterProvider) {
    $stateProvider
      .state('home', {
        url: '/',
        templateUrl: 'app/home/home.html',
        controller: 'HomeController',
        controllerAs: 'home'
      });

    $urlRouterProvider.otherwise('/');
  }

})();

/* global malarkey:false, moment:false */
(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .constant('malarkey', malarkey)
    .constant('moment', moment)
    .constant('FIREBASE_URL', 'https//winemaker-notes.firebaseio.com');

})();

(function() {
  'use strict';

  angular
    .module('detroitstartupjobs')
    .config(config);

  /** @ngInject */
  function config($logProvider, toastrConfig, $mdThemingProvider) {
    // Enable log
    $logProvider.debugEnabled(true);

    // Set options third-party lib
    toastrConfig.allowHtml = true;
    toastrConfig.timeOut = 3000;
    toastrConfig.positionClass = 'toast-top-right';
    toastrConfig.preventDuplicates = true;
    toastrConfig.progressBar = true;

    // set our material design theme
    $mdThemingProvider.theme('default')
     .primaryPalette('cyan')
     .accentPalette('teal')
     .backgroundPalette('grey');
  }

})();

angular.module("detroitstartupjobs").run(["$templateCache", function($templateCache) {$templateCache.put("app/home/home.html","<md-toolbar class=\"md-whiteframe-z3\"><div class=\"md-toolbar-tools\"><md-button ng-click=\"home.toggleNav()\" class=\"md-icon-button\" aria-label=\"Settings\"><md-icon md-font-library=\"material-icons\">menu</md-icon></md-button><h2><span>Detroit Startup Jobs</span></h2><span flex=\"\"></span><md-button class=\"md-icon-button\" aria-label=\"Saved Jobs\"><md-icon md-font-library=\"material-icons\">bookmark</md-icon></md-button><md-button class=\"md-icon-button\" aria-label=\"Search\"><md-icon md-font-library=\"material-icons\">search</md-icon></md-button><md-button class=\"md-icon-button\" aria-label=\"Notifications\"><md-icon md-font-library=\"material-icons\">add_alert</md-icon></md-button><md-button class=\"md-icon-button\" aria-label=\"Account\"><md-icon md-font-library=\"material-icons\">account_circle</md-icon></md-button></div></md-toolbar><div layout=\"row\"><div flex=\"\"></div><md-content flex=\"80\" layout=\"column\"><md-subheader>New Job Postings:</md-subheader><md-card><md-list class=\"md-no-style\" layout-fill=\"\" style=\"padding: 0px;\"><md-list-item ng-repeat=\"job in home.jobs\" class=\"md-3-line\" ng-click=\"goToJob()\" layout-fill=\"\"><md-icon md-font-library=\"material-icons\" ng-class=\"md-avatar-icon\">favorite</md-icon><div class=\"md-list-item-text\"><h3>{{ job.title }}</h3><p>{{ job.description }}</p><md-chips readonly=\"true\" ng-model=\"job.skills\" ng-class=\"md-primary\"></md-chips></div><md-divider></md-divider></md-list-item></md-list></md-card></md-content><div flex=\"\"></div></div>");}]);